.accordion__section {
  display: flex;
  flex-direction: column;
}

.accordion {
  background-color:  #ec546b;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.35s ease;
  position: relative;
}

.acordionTitle {
  position: absolute;
  z-index: 2;
  font-size: 6vw;
  color: white;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  text-align: center;
  letter-spacing: 2vw;
}
.spotlightPic {
  display: block;
  max-height: 100%;
  max-width: 100%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  filter: brightness(65%);
}

.accordion__icon {
  position: absolute;
  cursor: pointer;
  margin-left: auto;
  transition: transform 0.35s ease;
  z-index: 2;
  transform: translateY(-50%) translateX(-50%) rotate(0deg);
  left: 50%;
  top: 75%;
  width: 6vw;
}

.rotate {
  transform: translateY(-50%) translateX(-50%) rotate(90deg);
}

.accordion__content {
  background-color:  #ec546b;
  overflow: hidden;
  transition: max-height 0.35s ease;
}

.spotlightContent {
  margin: 50px 0px 50px 0px;
  text-align: center !important;
}

.spotlightTitle {
  font-weight: bold;
  font-size: 18px;
  margin: auto;
  color: white;
  font-family: "mark-regularregular", Helvetica, Arial, sans-serif;
  text-align: center !important;
  letter-spacing: 1.5px;
}

.spotlightDescription {
  padding: 25px 0px 25px 0px;
  margin: auto;
  color: white;
  font-size: 18px;
  font-family: "mark-regularregular", Helvetica, Arial, sans-serif;
  text-align: center !important;
}

.spotlightLinkHolder {

  color: white;
  font-size: 18px;
  font-family: "mark-regularregular", Helvetica, Arial, sans-serif;
  text-align: center !important;
}

.spotlightLink {
  color: white;
  font-size: 18px;
  font-family: "mark-regularregular", Helvetica, Arial, sans-serif;
  text-align: center !important;
}

.spotlightLink:hover {
  color: black;
  text-decoration: none;
}

@media (max-width: 768px) {
  .spotlightDescription {
    width: 75%;
  }
  .accordion__icon {
    width: 45px;
  }
}
@media (min-width: 768px) {
 
  .spotlightDescription,
  .spotlightLink,
  .spotlightTitle {
    font-size: 22px;
    line-height: 34px;
    width: 65%;
  }
  .acordionTitle {
    font-size: 56px;
    letter-spacing: 12px;
  }
}
@media (min-width: 1200px) {
  .spotlightDescription {
    width: 55%;
  }
  .acordionTitle {
    font-size: 72px;
    letter-spacing: 15px;
  }
  .accordion__icon {
    width: 80px;
  }
}
@media (min-width: 1800px) {
  .spotlightDescription {
    width: 40%;
  }
}
